<template>
  <el-row class="bgf">
    <el-form label-width="100px" :model="forminfo" :rules="rules" ref="forminfo">
      <el-tabs v-model="tabIndex">
        <el-tab-pane label="" name="1">
          <el-col :span="12" :offset="5">
            <el-form-item label="" prop="first_cateid">
              <el-select v-model="forminfo.first_cateid" @change="topChange" placeholder="">
                <el-option label="" value></el-option>
                <el-option
                  v-for="item in toplist"
                  :label="item.catename"
                  :value="item.id"
                  :key="item.id"
                ></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="" prop="first_cateid">
              <el-select v-model="forminfo.second_cateid" placeholder="">
                <el-option label="" value></el-option>
                <el-option
                  v-for="item in secondlist"
                  :label="item.catename"
                  :value="item.id"
                  :key="item.id"
                ></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="" prop="goodsname">
              <el-input v-model="forminfo.goodsname"></el-input>
            </el-form-item>
            <el-form-item label="" prop="price">
              <el-input v-model="forminfo.price"></el-input>
            </el-form-item>
            <el-form-item label="" prop="market_price">
              <el-input v-model="forminfo.market_price"></el-input>
            </el-form-item>
            <el-form-item label="" prop="img">
              <!--  -->
              <!-- action     -->
              <!-- list-type  -->
              <!-- file-list   [{name:'',url:""}] -->
              <!-- limit -->
              <!-- on-change:  -->
              <!-- name  -->
              <!-- auto-upload: -->
              <el-upload
                action="#"
                list-type="picture-card"
                :on-preview="handlePictureCardPreview"
                :file-list="fileList"
                :limit="4"
                :on-change="fileChange"
                :auto-upload="false"
                :on-exceed="limitImg"
              >
                <i slot="default" class="el-icon-plus"></i>
              </el-upload>
              <!--  -->
              <el-dialog :visible.sync="dialogVisible">
                <img width="100%" :src="dialogImageUrl" alt />
              </el-dialog>
            </el-form-item>
            <el-form-item label="" prop="specsid">
              <el-select v-model="forminfo.specsid" @change="specsChange" placeholder="">
                <el-option label="" value></el-option>
                <el-option
                  v-for="item in specslist"
                  :label="item.specsname"
                  :value="item.id"
                  :key="item.id"
                ></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="" prop="specsattr">
              <el-select v-model="forminfo.specsattr" multiple  placeholder="">
                <el-option label="" value></el-option>
                <el-option
                  v-for="item in specsvaluelist"
                  :label="item"
                  :value="item"
                  :key="item"
                ></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="" prop="isnew">
              <el-radio v-model="forminfo.isnew" :label="1"></el-radio>
              <el-radio v-model="forminfo.isnew" :label="0"></el-radio>
            </el-form-item>
            <el-form-item label="" prop="ishot">
              <el-radio v-model="forminfo.ishot" :label="1"></el-radio>
              <el-radio v-model="forminfo.ishot" :label="0"></el-radio>
            </el-form-item>
            <el-form-item label="" prop="status">
              <el-radio v-model="forminfo.status" :label="1"></el-radio>
              <el-radio v-model="forminfo.status" :label="0"></el-radio>
            </el-form-item>
          </el-col>
        </el-tab-pane>
        <el-tab-pane label="" name="2">
          <el-col :span="20">
              <!--  -->
              <vue-wangeditor id="editor" ref="editor"  width="100%"  v-model="forminfo.description"></vue-wangeditor>
              <br><br>
          </el-col>
        </el-tab-pane>
      </el-tabs>
      <br>
      <el-form-item>
        <el-button type="primary" v-if="!$route.params.id" @click="submit"></el-button>
        <el-button type="success" v-else @click="submit"></el-button>
        <el-button @click="reset"></el-button>
      </el-form-item>
    </el-form>
  </el-row>
</template>

<script>
// 
import vueWangeditor from 'vue-wangeditor'
// 
//    this.$route.params.id  
import {  getCategory,getChildCategory } from "@/request/category";
import {  getSpecs } from "@/request/specs"
import {  addGoods,editGoods,getGoods } from "@/request/goods"
let defaultInfo = {}; // 
export default {
  data() {
    return {
      forminfo: {
        first_cateid: "", // id
        second_cateid: "", // id
        goodsname: "", // 
        price: "", // 
        market_price: "", // 
        description: "", // 
        specsid: "", // id
        specsattr: [], // 
        isnew: 0, // 
        ishot: 0, // 
        img: "", // 
        status: 1 // 
      },
      rules: {
        // 
        first_cateid: [
          { required: true, message: "", trigger: "blur" }
        ],
        second_cateid: [
          { required: true, message: "", trigger: "blur" }
        ],
        goodsname: [
          { required: true, message: "", trigger: "blur" }
        ],
        price: [{ required: true, message: "", trigger: "blur" }],
        market_price: [{ required: true, message: "", trigger: "blur" }],
        specsid: [{ required: true, message: "", trigger: "blur" }],
        specsattr: [{ required: true, message: "", trigger: "blur" }]
      },
      tabIndex: "1",
      toplist: [], //,
      secondlist: [], // ,
      specslist:[],  //
      specsvaluelist:[], // 
      dialogVisible: false, // 
      dialogImageUrl: "", // 
      fileList: [] // 
    };
  },
  created() {
    this.getlist(); // 
    if (this.$route.params.id) {
      // 
      this.getinfo(this.$route.params.id);
    }
  },
  methods: {
    async getlist() {
      // 
      this.toplist = await getCategory();
      // 
      this.specslist = await getSpecs();
      
    },
    async getinfo(id) {
      // 
      this.forminfo = await getGoods({id});
      // id
      this.topChange(this.forminfo.first_cateid);     
      // 
      let url = this.forminfo.img ? this.$HOST + this.forminfo.img : "";
      if (url) {
        this.fileList.push({ name: this.forminfo.catename, url });
      }
      // 
      // IDID
      this.specsChange(this.forminfo.specsid)
      this.forminfo.specsattr = this.forminfo.specsattr.split(",")

      // 
      defaultInfo = { ...this.forminfo };
    },
    submit() {
      //  
      this.$refs.forminfo.validate(async isok => {
        if (isok) {
          // 
          // 
          this.forminfo.description = this.$refs.editor.getHtml();
          // 
          this.forminfo.specsattr = this.forminfo.specsattr.join(",")
          // FormData
          let formdata = new FormData();
          for (let k in this.forminfo) {
            formdata.append(k, this.forminfo[k]);
          }
          let res;
          if (this.$route.params.id) {
            formdata.append("id", this.$route.params.id); // id
            res = await editGoods(formdata); // 
          } else {
            // formdata
            res = await addGoods(formdata); // 
          }
          if (res) {
            this.$router.back();
          }
        } else {
          this.$message.warn("");
        }
      });
    },
    reset() {
      // 
      // resetFields
      if (this.$route.params.id) {
        this.fileList = [];
        this.getinfo(this.$route.params.id);
      } else {
        // 
        this.$refs.forminfo.resetFields();
        this.fileList = [];
      }
    },
    // 
    handlePictureCardPreview(file) {
      // 
      this.dialogImageUrl = file.url;
      this.dialogVisible = true;
    },
    fileChange(file) {
      // 
      // file.raw 
      this.forminfo.img = file.raw;
    },
    limitImg() {
      this.$message.warning("4");
    },
    async topChange(val){
        // 
        if(val){ // valid
           this.secondlist  = await getChildCategory(val);
        }else{
          this.secondlist = [];
          this.forminfo.second_cateid = "";
        }
    },
    async specsChange(val){
        if(val){ // valid
           let res  = await getSpecs(val);
           this.specsvaluelist = res.attrs;
        }else{
          this.specsvaluelist = [];
          this.forminfo.specsattr = [];
        }
    }

  },
  components: { 
    vueWangeditor
  }
};

// 
// 
// 1 
// 2   FormData 
// let formdata =  new FormData();
// formdata.append('',)
// ajax , formdata


// wangeditor  
  // npm install --save vue-wangeditor
