    <vue-wangeditor id="editor" v-model="text></vue-wangeditor>
  </template>

  import vueWangeditor from 'vue-wangeditor'
  export default {
    ...
    components: {
      vueWangeditor
    }
  }

  */

</script>
<style scoped>
</style>


